import { Component, OnInit } from '@angular/core';
import { QuizService } from '../dashboard/services/quiz.service';
import { Quiz } from '../dashboard/services/quiz';
import { Material } from '../dashboard/services/material';
import { MaterialService } from '../dashboard/services/material.service';

@Component({
  selector: 'app-quiz-levels',
  templateUrl: './quiz-levels.component.html',
  styleUrls: ['./quiz-levels.component.css'],
  providers: [QuizService, MaterialService]
})
export class QuizLevelsComponent implements OnInit {
  quizlevels: Quiz[];
  materiallist: Material[];

  constructor(private quizService: QuizService, private materialService: MaterialService) { }

  ngOnInit() {
    var x = this.quizService.getData();
    x.snapshotChanges().subscribe(item => {
      this.quizlevels = [];
      item.forEach(element => {
        var y = element.payload.toJSON();
        y["key"] = element.key;
        this.quizlevels.push(y as Quiz);
      });
    });

    var m = this.materialService.getData();
    m.snapshotChanges().subscribe(item => {
      this.materiallist = [];
      item.forEach(element => {
        var ml = element.payload.toJSON();
        ml["key"] = element.key;
        this.materiallist.push(ml as Material);
      });
    });
  }

}
